import {
  FamilyImgList,
  Strategy,
  StrategyUuid,
  StrategyConfig,
  CmData,
  MallButtonConfig,
  MallOrder,
  SceneAdmin,
  StartMode,
  AppHomeConfig,
  HomeConfig,
  MusicConfig
} from '../../pages/index.js'

var configManage = [
  {// 
    path: 'familyImgList',
    name: 'familyImgList',
    component: FamilyImgList
  },
  {// 
    path: 'Strategy',
    name: 'Strategy',
    component: Strategy
  },
  {// 
    path: 'StrategyUuid',
    name: 'StrategyUuid',
    component: StrategyUuid
  },
  {// 
    path: 'StrategyConfig',
    name: 'StrategyConfig',
    component: StrategyConfig
  },
  {// 
    path: 'CmData',
    name: 'CmData',
    component: CmData
  },
  // {//
  //   path: 'appHomeConfig',
  //   name: 'appHomeConfig',
  //   component: AppHomeConfig
  // },
  { // 
    path: 'appHomeConfig',
    name: 'appHomeConfig',
    component: AppHomeConfig,
    children: [
      {
        path: 'home',
        name: 'home',
        component: HomeConfig
      },
      {
        path: 'music',
        name: 'music',
        component: MusicConfig
      }
    ]
  },

  // 
  {//
    path: 'mallButtonConfig',
    component: MallButtonConfig
  },
  {
    path: 'mallOrder',
    component: MallOrder
  },
  // 
  {
    path: 'sceneAdmin',
    component: SceneAdmin
  },
  // // 
  // {
  //   path: 'sightManage',
  //   component: SightManage
  // },
  { // startMode
    path: 'startMode',
    component: StartMode
  }
]

export default configManage
