import { dateFormat, isType } from '@/assets/js/utils.js'
/**
 *table item
 * label element table
 *options 
 * nameoption array [
 { label: '', value: 1 },
 { label: '', value: 2 },
 { label: '', value: 3 }
 ]
 opttions {
      1: '',
      2: '',
      3: ''
    }
 *
 */
export const format = (value, label, options) => {
  if (!value) return ''
  let reg = /(|)/g
  if (reg.test(label)) {
    return dateFormat(value)
  }
  if (options) {
    if (isType('array', options)) {
      let result = ''
      options.forEach(item => {
        if (item.value + '' === value + '') {
          result = item.label || ''
        }
      })
      return result
    }
    if (isType('object', options)) {
      return options[value] || ''
    }
  }
  return value
}
